.nav--closed {
  display: none;
}

.nav--opened {
  display: block;
}

.nav--nojs {
  display: block;
}

.nav__list {
  @include list-reset;
}

.nav__item {
  border-top: 1px solid $grey-border;

  &:last-child {
    border-bottom: none;
  }
}

.nav__link {
  display: block;
  margin: 0;
  padding: 24px 0;

  @include font(20px, 20px, $font-medium, $black-two);
  text-align: center;
  text-transform: uppercase;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.2;
  }
}


.nav__toggle {
  display: block;
  width: 25px;
  height: 19px;
  margin-left: auto;

  background: url("../img/icon-mobile-menu.svg") center top no-repeat transparent;

  cursor: pointer;
  border: none;
}

.nav__toggle--opened {
  width: 19px;
  height: 19px;

  background: url("../img/icon-close.svg") center top no-repeat transparent;
}

/**
 * Tablet
 */
@media (min-width: $tablet) {
  .nav__toggle {
    display: none;
  }

  .nav--closed {
    display: block;
  }

  .nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  .nav__item {
    border: none;
    margin-right: 37px;

    &:last-child {
      margin-right: 0;
    }
  }

  .nav__link {
    margin-bottom: -6px;
    padding: 0 0 6px;

    border-bottom: 2px solid transparent;

    @include font(18px, 25px, $font-regular, $black);
  }

  .nav__link--active {
    border-bottom-color: $green-leaf;
  }
}

/**
 * Desktop
 */
@media (min-width: $desktop) {
  .nav__list {
    max-width: 600px;
  }

  .nav__item {
    margin-bottom: 10px;
    margin-right: 34px;
  }

  .nav__link {
    @include font(20px, 25px, $font-regular, $black-two);

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .nav__link--home {
    @include font(20px, 25px, $font-regular, $white);
  }

  .header--home .nav__link--active {
    border-bottom-color: $white;
  }
}
